// fonts
@include font('PTSans-Regular', 'PTSans-Regular');
@include font('OpenSans', 'OpenSans');
@include font('OpenSans-Extrabold', 'OpenSans-Extrabold');

$default-fonts: Arial, sans-serif;
@mixin pt {
	font-family: 'PTSans-Regular', $default-fonts; }
@mixin open {
	font-family: 'OpenSans', $default-fonts; }
@mixin open-extrabold {
	font-family: 'OpenSans-Extrabold', $default-fonts; }

// colors
$red: #a70a3f;
$gray-light: #a2a4a8;
$black: #000000;
$gray-dark: #808080;
$white: #ffffff;
$whitesmoke: #f5f5f5;

//title
@mixin h1-lg {
	@include open-extrabold;
	font-size: 35px;
	line-height: 1.28571;
	@include r(767) {
		font-size: 30px;
		line-height: 36px; } }
@mixin h1 {
	@include open-extrabold;
	font-size: 24px;
	line-height: 1.25; }
@mixin h2 {
	@include open-extrabold;
	font-size: 18px;
	line-height: 1.33333; }
@mixin h3 {
	@include open-extrabold;
	font-size: 12px;
	line-height: 1.5; }

@mixin trs {
	transition: all 0.5s ease-in-out; }

@mixin smooth {
	-webkit-font-smoothing: antialiased; }

@mixin text-slider {
	@include open-extrabold;
	font-size: 60px; }
