h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	color: inherit;
	font-weight: inherit; }

.motion {
	position: relative;
	overflow: hidden;
	&__element {
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0; } }

.btn {
	position: relative;
	display: inline-block;
	width: 300px;
	height: 60px;
	line-height: 60px;
	@include open-extrabold;
	font-size: 12px;
	color: $white;
	text-align: center;
	text-transform: uppercase;
	@include r(1279) {
		width: 235px; }
	@include r(767) {
		width: 290px; }
	&__hide {
		@include r(1279) {
			display: none; }
		&-mobile {
			@include r(767) {
				display: none; } } }
	.btn__icon {
		display: inline-block;
		vertical-align: middle;
		height: 14px;
		width: 3px;
		position: relative;
		left: 15px;
		top: 0;
		transition: all 0.5s ease-out;
		transform: translate3d(0,0,0);
		&:before,
		&:after {
			content: '';
			position: absolute;
			display: block;
			right: 0;
			width: 3px;
			height: 8px;
			background-color: #ffffff;
			transition: all 0.5s ease-out;
			transform: translate3d(0,0,0); }
		&:before {
			margin-top: 4px;
			bottom: 50%; }
		&:after {
			margin-top: -4px;
			top: 50%; }
		&_red {
			&:before,
			&:after {
				background-color: $red; } } }
	&:hover &__icon {
		&:before {
			transform: rotate(-45deg) translateZ(0); }
		&:after {
			transform: rotate(45deg) translateZ(0); } }
	&_red {
		background-color: $red; }
	&_black {
		background-color: $black; }
	&_white {
		color: $red;
		background-color: #ffffff; } }

.arrow-bot-anim {
	display: inline-block;
	vertical-align: middle;
	height: 14px;
	width: 3px;
	position: relative;
	transform: rotate(90deg) translateZ(0);
	transition: all 0.5s ease-out;
	&:before,
	&:after {
		content: '';
		position: absolute;
		display: block;
		right: 0;
		width: 3px;
		height: 8px;
		background-color: #000;
		transition: all 0.5s ease-out;
		transform: translate3d(0,0,0); }
	&:before {
		margin-top: 4px;
		bottom: 50%; }
	&:after {
		margin-top: -4px;
		top: 50%; }
	&.is-active {
		&:before {
			transform: rotate(-45deg) translateZ(0); }
		&:after {
			transform: rotate(45deg) translateZ(0); } } }


/* ===================================
	 Main Styles
	 @mixin ================================== */
.main {
	position: relative;
	z-index: 1;
	padding-top: 121px;
	@include r(1279) {
		padding-top: 105px; }
	@include r(767) {
		padding-top: 93px; } }

.line-vertical {
	pointer-events: none;
	position: absolute;
	z-index: 1;
	top: 0;
	width: 1px;
	bottom: 0;
	@include r(767) {
		display: none; }
	&:before,
	&:after {
		content: '';
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		background-color: #f5f5f5; }
	&:before {
		@include r(1279) {
			display: none;
			content: none; } }
	&_left {
		margin-left: -601px;
		left: 50%;
		@include r(1279) {
			margin-left: 0;
			left: 0; }
		&:after {
			transform: translateX(300px);
			z-index: 2;
			@include r(1279) {
				transform: none;
				left: calc(33.33vw + (24px - 15px) / 3); } } }
	&_right {
		margin-right: -601px;
		right: 50%;
		@include r(1279) {
			right: 0;
			margin-right: 0; }
		&:after {
			transform: translateX(-300px);
			@include r(1279) {
				transform: none;
				left: auto;
				right: calc(33.33vw + (24px - 15px) / 3); } } }
	&_mid {
		left: 50%;
		@include r(1279) {
			display: none; }
		&:after {
			display: none;
			content: none; } } }

body {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	position: relative;
	min-width: 320px;
	@include pt;
	font-size: 14px;
	color: #000;
	line-height: 1.2;
	@include smooth;
	background-color: #ffffff;
	&.is-overflow {
		position: fixed;
		overflow: hidden;
		top: 0;
		left: 0;
		right: 0; } }

.wrapper {
	position: relative;
	overflow: hidden;
	flex: 1 0 auto; }

.main {
	position: relative; }

.container {
	position: relative;
	z-index: 2;
	margin-left: auto;
	margin-right: auto;
	width: 100%;
	max-width: 1200px;
	@include r(1279) {
		max-width: calc(100% - 48px); }
	@include r(767) {
		max-width: calc(100% - 30px); } }

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 1000;
	background-color: #ffffff;
	transition: all 1s ease-in-out;
	&__container {
		position: absolute;
		z-index: 1;
		width: 240px;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%) scale(1);
		transition: transform 1s ease-in-out, opacity 0.25s linear; }
	&__rect {
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		right: 0;
		background-color: #ffffff;
		height: 61px;
		overflow: hidden;
		&:before {
			content: '';
			position: absolute;
			z-index: 3;
			top: 5px;
			left: 0;
			height: 50px;
			width: 100%;
			background-color: #ffffff;
			animation: preloader-text 0.75s forwards linear; }
		&-top {
			position: absolute;
			z-index: 4;
			top: 0;
			height: 6px;
			left: 0;
			right: 0;
			background-color: #ffffff;
			animation: preloader-top 0.25s forwards ease-in-out;
			animation-delay: 0.75s; }
		&-bottom {
			position: absolute;
			z-index: 4;
			bottom: 0;
			height: 6px;
			left: 0;
			right: 0;
			background-color: #ffffff;
			animation: preloader-bottom 0.25s forwards ease-in-out;
			animation-delay: 1.24s; }
		&-left {
			position: absolute;
			z-index: 4;
			top: 0;
			width: 6px;
			left: 0;
			bottom: 0;
			background-color: #ffffff;
			animation: preloader-left 0.25s forwards ease-in-out;
			animation-delay: 0.99s; }
		&-right {
			position: absolute;
			z-index: 4;
			top: 0;
			width: 6px;
			right: 0;
			bottom: 0;
			background-color: #ffffff;
			animation: preloader-right 0.25s forwards ease-in-out;
			animation-delay: 1.49s; } }
	&__text {
		position: absolute;
		z-index: 1;
		top: -30px;
		left: 0;
		right: 0;
		animation: svg-text 2.5s forwards ease-in-out; }
	.is-loaded & {
		opacity: 0;
		pointer-events: none; }
	.is-loaded &__container {
		opacity: 0;
		transform: translate(-50%,-50%) scale(0.9); } }

@keyframes preloader-top {
	0% {
		transform: translateX(0); }
	100% {
		transform: translateX(-100%); } }

@keyframes preloader-left {
	0% {
		transform: translateY(0); }
	100% {
		transform: translateY(100%); } }

@keyframes preloader-bottom {
	0% {
		transform: translateX(0); }
	100% {
		transform: translateX(100%); } }

@keyframes preloader-right {
	0% {
		transform: translateY(0); }
	100% {
		transform: translateY(-100%); } }

@keyframes preloader-text {
	0% {
		transform: translateX(0); }
	10% {
		transform: translateX(0); }
	75% {
		transform: translateX(100%); }
	100% {
		transform: translateX(100%); } }

@keyframes svg-text {
	0% {
		top: -30px; }
	75% {
		top: -30px; }
	100% {
		top: 0; } }


