/* ===================================
	 Hambruger
	 @mixin ================================== */
.hamburger {
	display: none;
	position: relative;
	z-index: 101;
	padding: 11px 5px;
	margin-right: -5px;
	pointer-events: auto;
	-webkit-tap-highlight-color: transparent;
	cursor: pointer;
	transition-property: opacity, -webkit-filter;
	transition-property: opacity, filter;
	transition-property: opacity, filter, -webkit-filter;
	transition-duration: 0.15s;
	transition-timing-function: linear;
	font: inherit;
	color: inherit;
	text-transform: none;
	background-color: transparent;
	border: 0;
	overflow: visible;
	transition: all 0.5s;
	@include r(1279) {
		float: right;
		display: block; }
	@include r(767) {
		padding-top: 4px;
		padding-bottom: 4px; }
	&__box {
		width: 23px;
		height: 16px;
		display: block;
		position: relative; }
	&__inner {
		display: block;
		top: 50%;
		margin-top: -1px;
		width: 23px;
		height: 2px;
		background-color: $gray-light;
		position: absolute;
		transition-property: -webkit-transform;
		transition-property: transform;
		transition-property: transform, -webkit-transform;
		transition-duration: 0.15s;
		transition-timing-function: ease;
		&::before, &::after {
			width: 23px;
			height: 2px;
			background-color: $gray-light;
			position: absolute;
			transition-property: -webkit-transform;
			transition-property: transform;
			transition-property: transform, -webkit-transform;
			transition-duration: 0.15s;
			transition-timing-function: ease; }
		&::before, &::after {
			content: "";
			display: block; }
		&::before {
			top: -7px; }
		&::after {
			bottom: -7px; } }
	&.is-active &__inner {
		background-color: #000000; }
	&.is-active &__inner:before,
	&.is-active &__inner:after {
		background-color: #000000; }
	&_spin &__inner {
		transition-duration: 0.3s;
		transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
		&::before {
			transition: top 0.1s 0.34s ease-in, opacity 0.1s ease-in; }
		&::after {
			transition: bottom 0.1s 0.34s ease-in, -webkit-transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);
			transition: bottom 0.1s 0.34s ease-in, transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19);
			transition: bottom 0.1s 0.34s ease-in, transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19), -webkit-transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19); } }
	&.is-active &__inner {
		-webkit-transform: rotate(225deg);
		transform: rotate(225deg);
		transition-delay: 0.14s;
		transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
		&::before {
			top: 0;
			opacity: 0;
			transition: top 0.1s ease-out, opacity 0.1s 0.14s ease-out; }
		&::after {
			bottom: 0;
			-webkit-transform: rotate(-90deg);
			transform: rotate(-90deg);
			transition: bottom 0.1s ease-out, -webkit-transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
			transition: bottom 0.1s ease-out, transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);
			transition: bottom 0.1s ease-out, transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1); } } }


/* ===================================
	 Header
	 @mixin ================================== */
.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	background-color: lighten($red,30);
	opacity: 0;
	transform: translateY(-40px);
	transition: all 1s ease-in-out;
	.is-loaded & {
		opacity: 1;
		transform: translateY(0); }
	&__container {
		@include pie-clearfix; }
	&__top {
		position: relative;
		z-index: 2;
		@include open-extrabold;
		color: #ffffff;
		line-height: 38px;
		background-color: #000000;
		opacity: 0;
		transform: translateY(-40px);
		transition: all 1s ease-in-out;
		@include pie-clearfix;
		.is-loaded & {
			opacity: 1;
			transform: translateY(0); } }
	&__phone {
		float: left;
		position: relative;
		padding-left: 57px;
		@include r(767) {
			padding-left: 0; }
		&:before {
			content: '';
			position: absolute;
			z-index: 1;
			top: 50%;
			left: 0;
			width: 30px;
			height: 1px;
			background-color: rgba(#ffffff, .2);
			@include r(767) {
				display: none;
				content: none; } } }
	&__login {
		float: right;
		font-size: 12px;
		color: rgba(#ffffff, .4);
		text-transform: uppercase;
		@include trs;
		&:hover {
			color: $red; } }
	&__lang {
		position: relative;
		position: relative;
		z-index: 101;
		float: right;
		margin-right: 30px;
		font-size: 12px;
		text-transform: uppercase;
		&-link {
			display: block;
			position: relative;
			padding-left: 10px;
			padding-right: 30px;
			color: $gray-dark;
			line-height: 30px;
			background-color: #ffffff;
			@include trs;
			&:before {
				content: '';
				position: absolute;
				z-index: 1;
				top: 50%;
				right: 9px;
				width: 0;
				height: 3px;
				margin-top: -2px;
				background-color: transparent;
				@include trs; } }
		&_parent > &-item > &-link {
			&:before,
			&:after {
				content: '';
				position: absolute;
				display: block;
				top: 50%;
				right: 9px;
				width: 8px;
				height: 3px;
				background-color: #4C4C4C;
				transition: all 0.5s ease-in-out; }
			&:after {
				right: 13px;
				margin-top: -2px; } }
		&_parent.is-active > &-item > &-link {
			&:before,
			&:after {
				background-color: #c9c9c9; }
			&:before {
				margin-top: -3px;
				transform: rotate(-45deg); }
			&:after {
				margin-top: -3px;
				transform: rotate(45deg); } }
		&:not(&_parent) &-item:hover > &-link {
			color: $red;
			&:before {
				width: 13px;
				background-color: $red; } }
		&-item.active > &-link {
			color: #ffffff;
			line-height: 38px; }
		&-item.active > &-link {
			background-color: transparent; }
		&.is-active > &-item > &-link {
			color: $red;
			background-color: #ffffff; }
		& & {
			display: none;
			position: absolute;
			z-index: 1;
			top: 100%; } }
	&__bot {
		padding: 14px 0;
		position: absolute;
		z-index: 1;
		top: 100%;
		left: 0;
		right: 0;
		border-bottom: 1px solid #f5f5f5;
		transform: translateY(40px);
		transition: all 1s ease-in-out;
		@include r(1279) {
			padding: 14px 0 11px; }
		.is-loaded & {
			transform: translateY(0); } }
	&__logo {
		float: left;
		color: $red;
		font-size: 0;
		.icon {
			font-size: 53px;
			@include r(1279) {
				font-size: 41px; }
			@include r(767) {
				font-size: 29px; } } }
	&__nav {
		float: right;
		margin-right: -35px; } }

/* Nav
	----------------------------------- */
.nav {
	@include open-extrabold;
	font-size: 12px;
	color: $gray-light;
	text-transform: uppercase;
	@include pie-clearfix;
	&__item {
		float: left;
		padding: 20px 35px 20px 36px; }
	&_header {
		@include trs;
		@include r(1279) {
			display: none;
			flex-direction: column;
			position: absolute;
			justify-content: center;
			left: -24px;
			right: -24px;
			top: -14px;
			height: calc(100vh - 38px);
			background-color: #ffffff;
			flex-wrap: nowrap;
			width: calc(100% + 48px);
			&.is-active {
				display: flex;
				overflow-y: auto; } }
		@include r(767) {
			justify-content: flex-start;
			padding-top: 30px;
			padding-bottom: 30px;
			left: -15px;
			right: -15px;
			height: calc(100vh - 38px);
			width: calc(100% + 30px); } }
	@include r(1279) {
		&_header &__item {
			color: #000000;
			float: none;
			padding: 15px 0;
			text-align: center;
			font-size: 24px;
			line-height: 1; } }
	&_header &__item {
		opacity: 0;
		transform: translateY(-15px);
		@include r(1279) {
			transform: none; } }
	&__link {
		display: block;
		@include trs;
		&:hover {
			color: $red; } }
	&_header &__item.active &__link {
		color: $red;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: -34px;
			left: 0;
			right: 0;
			height: 2px;
			background-color: $red;
			@include r(1279) {
				display: none; } } }
	&_footer {
		color: $gray-light; }
	&_footer &__item.active &__link {
		color: $red; }
	@include r(1279) {
		&_footer {
			overflow: hidden;
			display: flex;
			width: 100%;
			flex-direction: column;
			flex-wrap: wrap;
			height: 140px;
			@include r(767) {
				max-height: 160px; } }
		&_footer &__item {
			float: none;
			display: block;
			padding-top: 13px;
			padding-bottom: 13px;
			width: auto;
			flex: 0 0 33.33%;
			@include r(767) {
				padding-top: 10px;
				padding-bottom: 10px;
				flex: 0 0 25%; } } }
	&_footer &__item {
		padding-left: 24px;
		padding-right: 25px; } }


/* ===================================
	 Footer
	 @mixin ================================== */
.footer {
	width: 100%;
	background-color: lighten($red,30);
	border-top: 1px solid #f5f5f5;
	&__top {
		overflow: hidden;
		padding-top: 12px;
		padding-bottom: 12px;
		background-color: #ffffff; }
	&__container {
		overflow: hidden; }
	&__nav {
		float: left;
		margin-left: -24px;
		@include r(1279) {
			float: none; } }
	&__socials {
		margin-top: 15px;
		margin-right: -5px;
		float: right;
		@include r(1279) {
			float: none;
			position: absolute;
			left: 66.66%;
			top: 33px;
			margin-left: -3px; }
		@include r(767) {
			left: 50%;
			top: 90px;
			margin-left: -7px; } }
	&__bot {
		padding-top: 13px;
		padding-bottom: 10px;
		background-color: #000000;
		@include pie-clearfix; }
	&__copy {
		position: relative;
		top: 2px;
		float: left;
		font-size: 12px;
		line-height: 15px;
		color: rgba(#ffffff,.2); }
	&__vintage {
		float: right;
		font-size: 0;
		.icon {
			font-size: 15px;
			color: rgba(#ffffff, .4); } } }

/* Socials
	----------------------------------- */
.socials {
	@include pie-clearfix;
	&__item {
		float: left;
		& + & {
			margin-left: 9px; } }
	&__link {
		display: block;
		padding: 5px;
		font-size: 0;
		color: $gray-light;
		@include trs;
		&:hover {
			color: $red; } }
	&__item:nth-child(1) {
		.icon {
			font-size: 13px; } }
	&__item:nth-child(2) {
