/* ===================================
	 Home Page
	 @mixin ================================== */

/* Screen
	----------------------------------- */
.screen {
	margin-top: -38px;
	position: relative;
	color: #ffffff;
	min-height: 290px;
	.line-vertical {
		opacity: 0.1; }
	.line-vertical_left:after,
	.line-vertical_right:after {
		top: 121px;
		@include r(1279) {
			top: 104px; } }
	&__anime {
		pointer-events: none;
		position: absolute;
		z-index: 3;
		top: 120px;
		left: 0;
		right: 0;
		height: 0;
		@include r(1279) {
			display: none; }
		&:before {
			content: '';
			position: absolute;
			z-index: 1;
			left: 50%;
			margin-left: -611px;
			top: -1px;
			width: 20px;
			height: 3px;
			background-color: #ffffff;
			transition: all 1s ease-in-out;
			transition-delay: 0.5s; }
		&.is-loaded {
			&:before {
				transform: translateY(84px); } }
		&:after {
			content: '';
			position: absolute;
			z-index: 1;
			right: 50%;
			margin-right: -602px;
			width: 3px;
			height: 3px;
			background-color: #ffffff;
			transition: all 1s ease-in-out;
			transition-delay: 0.5s; }
		&.is-loaded {
			&:after {
				transform: translateY(84px); } } }
	&__item {
		position: relative;
		height: 100vh;
		overflow: hidden;
		@include r(1279) {
			height: 540px; } }
	&_sm &__item {
		height: 620px;
		@include r(1279) {
			height: 575px; }
		@include r(767) {
			height: 325px; } }
	&__slider.is-video &__item {
		pointer-events: none; }
	&__video {
		pointer-events: auto;
		position: absolute;
		opacity: 0;
		z-index: 1;
		top: 50%;
		left: 50%;
		transform: scale(1.1) translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		transition: all 1s ease-in-out;
		&.is-active {
			cursor: pointer;
			opacity: 1; }
		.is-loaded & {
			transform: scale(1) translate(-50%, -50%); } }
	&__img {
		position: absolute;
		z-index: 0;
		width: 100%;
		height: 100%;
		pointer-events: none;
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
		&_tablet {
			display: none;
			@include r(1279) {
				display: block; } }
		&_tablet + & {
			@include r(1279) {
				display: none; } }
		.slick-current & {
			transform: scale(1.1);
			transition: all 1s ease-in-out; }
		.is-loaded .slick-current & {
			transform: scale(1); } }
	&__container {
		position: relative;
		height: 100%;
		padding-top: 40px;
		z-index: 2;
		@include vertical; }
	&__play {
		position: absolute;
		z-index: 1;
		left: 50%;
		margin-left: -486px;
		top: 50%;
		margin-top: 23px;
		@include open-extrabold;
		font-size: 12px;
		color: rgba(#ffffff, .5);
		line-height: 1.5;
		text-transform: uppercase;
		@include trs;
		@include r(1279) {
