			right: 100%;
			margin-right: -18px;
			width: 42px;
			background-color: rgba(#f5f5f5, 0.1); }
		@include r(767) {
			display: none; }
		&:hover {
			color: #ffffff; }
		.is-loaded & {
			transform: translateX(0); }
		&_open-article {
			top: auto;
			margin-left: -330px;
			@include r(1279) {
				top: 0; } }
		&-text {
			display: inline-block;
			vertical-align: 2px;
			opacity: 0;
			transform: translateY(-30px);
			@include r(1279) {
				display: none; } }
		.icon {
			position: relative;
			top: -3px;
			margin-left: 17px;
			width: 28px;
			color: #ffffff;
			opacity: 0;
			@include r(1279) {
