			.path {
				fill: none;
				stroke: #fff;
				stroke-width: 3px;
				fill-rule: evenodd;
				stroke-dasharray: 132px;
				stroke-dashoffset: 132px;
				transition: all 1.25s ease-in;
				transition-delay: 1s; } }
		&:before {
			pointer-events: none;
			content: '';
			position: absolute;
			z-index: 1;
			width: 1px;
			height: 0;
			bottom: 100%;
			margin-bottom: 17px;
			right: 15px;
			background-color: #ffffff;
			transition: all 0.75s ease-in-out;
			transition-delay: 0.75s;
			@include r(1279) {
				bottom: 50%;
				right: 21px;
				margin-bottom: 45px; } }
		&:after {
			pointer-events: none;
			content: '';
			position: absolute;
			z-index: 1;
			width: 1px;
			height: 0;
			top: 100%;
			margin-top: 10px;
			right: 15px;
			background-color: #ffffff;
			transition: all 0.75s ease-in-out;
			transition-delay: 0.75s;
			@include r(1279) {
				margin-top: 15px;
				top: 50%;
				right: 21px; } }
		.is-loaded &:before,
		.is-loaded &:after {
			height: 34px; }
		.is-loaded & .path {
			stroke-dashoffset: 0; } }
