		opacity: 0;
		transform: translateX(-30px); }
	&__inner {
		margin-top: 125px;
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		@include r(1279) {
			margin-top: -45px; }
		@include r(767) {
			margin-top: -53px; } }
	&_sm &__inner {
