	&__slider.is-video &__title {
		opacity: 0;
		transform: translateX(50px); }
	&__btn {
		position: relative;
		margin-top: 40px;
		margin-left: 230px;
		@include r(1279) {
			position: absolute;
			left: 74.5%;
			top: 100%;
			margin-left: 0; }
		@include r(767) {
			left: auto;
			margin-left: auto;
			margin-right: auto;
			display: block; } }
	&__scroll {
		position: absolute;
		bottom: 25px;
		right: 70px;
		z-index: 1;
		@include open-extrabold;
		font-size: 12px;
		color: #ffffff;
		text-transform: uppercase;
		@include trs;
		@include r(1279) {
			right: 57px; }
		@include r(767) {
			display: none; }
		&-text {
			display: inline-block;
			vertical-align: middle;
			opacity: 0;
			transform: translateY(-20px); }
		&-line {
			position: absolute;
			z-index: 1;
			height: 65px;
			width: 3px;
			right: -31px;
			bottom: -25px;
			overflow: hidden;
			transform: translateY(-100%);
			&:before {
				content: '';
				position: absolute;
				z-index: 1;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				background-color: #ffffff; }
			&.is-animated {
				&:before {
					animation: scroll-btn 2s infinite cubic-bezier(0.46, 0.03, 0.52, 0.96);
					animation-delay: -1s; } } }
		&.is-video {
			pointer-events: none;
			opacity: 0;
			transform: translateY(50px); } }
	&__prev {
		position: absolute;
		z-index: 3;
		bottom: 15px;
		left: calc(50% - 615px);
		font-size: 0;
		@include r(1279) {
			left: 5px; }
		@include r(767) {
			bottom: 20px;
			left: 0;
			transform: rotate(180deg); } }
	&__next {
		position: absolute;
		z-index: 3;
		left: calc(50% - 325px);
		bottom: 15px;
		font-size: 0;
		@include r(1279) {
			left: 300px; }
		@include r(767) {
			bottom: 20px;
			left: auto;
			right: 0; } }
	&__prev,
	&__next {
		padding: 15px;
		color: rgba(#ffffff, .5);
		@include trs;
		&:hover {
			color: #ffffff; } }
	&__dots {
		position: absolute;
		z-index: 3;
		bottom: 15px;
		left: calc(50% - 530px);
		font-size: 0;
		@include trs;
		@include pie-clearfix;
		@include r(1279) {
			left: 95px; }
		@include r(767) {
			display: none; }
		&-item {
			cursor: pointer;
			display: inline-block;
			@include open-extrabold;
			font-size: 60px;
			color: #ffffff;
			& + & {
				position: relative;
				margin-left: 55px;
				font-size: 24px;
				color: rgba(#ffffff, .5);
				&:before {
					content: '';
					position: absolute;
					z-index: 1;
					width: 3px;
					height: 3px;
					pointer-events: none;
					background-color: #ffffff;
					top: 50%;
					margin-top: -2px;
					left: -28px; } } } }
	&__slider.is-video &__dots,
	&__slider.is-video &__prev,
	&__slider.is-video &__next {
		pointer-events: none;
		opacity: 0;
		transform: translateY(50px); } }

@keyframes scroll-btn {
	from {
		transform: translateY(-100%); }
	to {
		transform: translateY(100%); } }

