/* Start
	----------------------------------- */
.start {
	padding-top: 100px;
	font-size: 0;
	text-align: right;
	@include r(1279) {
		padding-top: 0; }
	@include r(767) {
		padding-top: 40px; }
	&_open-article {
		padding-bottom: 175px;
		@include r(1279) {
			padding-bottom: 90px; }
		@include r(767) {
			display: none; } }
	&__container {
		@include pie-clearfix;
		&:before {
			content: '';
			pointer-events: none;
			position: absolute;
			left: -2px;
			bottom: 0;
			width: 3px;
			height: 65px;
			background-color: #e8ebf1;
			@include r(1279) {
				display: none;
				content: none; } } }
	&__list {
		@include r(1279) {
			display: flex;
			flex-wrap: wrap;
			width: calc(100% + 30px);
			margin-left: -15px;
			margin-right: -15px;
			padding-left: 15px;
			padding-right: 15px;
			justify-content: center; } }
	&__item {
		position: relative;
		overflow: hidden;
		display: inline-block;
		vertical-align: top;
		width: 25%;
		text-align: left;
		transform: translate3d(0,0,0);
		@include r(1279) {
			width: calc(33.33% - 15px);
			flex: 1 0 auto;
			&:nth-child(3n+2) {
				width: 33.33%;
				@include r(767) {
					width: 100%; } }
			&:nth-child(1) {
				@include r(767) {
					order: 1; } }
			&:nth-child(2) {
				@include r(767) {
					order: 3; } }
			&:nth-child(3) {
				@include r(767) {
					order: 2; } }
			&:nth-child(4) {
				order: 6;
				width: calc(33.33% - 15px);
				@include r(767) {
					width: 100%;
					order: 4; } }
			&:nth-child(5) {
				order: 4;
				width: calc(33.33% - 15px);
				@include r(767) {
					width: 100%;
					order: 6; } }
			&:nth-child(6) {
				order: 5;
				width: 33.33%;
				@include r(767) {
					width: 100%; } } }
		@include r(767) {
			height: 113px;
			width: 100%;
			&:before {
				display: none;
				content: none; } }
		&:not(&_title) {
			@include ratio(1,1);
			@include r(767) {
				&:before {
					display: none;
					content: none; } } }
		&_red {
			color: #ffffff;
			.motion {
				&__inner {
					background-color: $red; } } }
		&_white {
			background-color: #ffffff;
			.motion {
				overflow: hidden;
				&:before {
					content: '';
					position: absolute;
					z-index: 0;
					left: 0;
					right: 0;
					top: 0;
					height: 1px;
					background-color: #f5f5f5; }
