		&-row {
			display: flex;
			flex-direction: row;
			justify-content: flex-end; }
		&-item {
			background-color: $red;
			@include trs;
			&:hover {
				background-color: $gray-light; }
			& + & {
				margin-left: 20px; } }
		&-link {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 40px;
			height: 40px;
			color: #ffffff;
			.icon-tw {
				font-size: 12px; }
			.icon-fb {
				font-size: 16px; } } } }

/* Subscribe
	----------------------------------- */
.subscribe {
	&__rel {
		position: relative;
		& > .motion {
			z-index: 2;
			& > .motion__inner {
				background-color: #f5f5f5; }
			& > .motion__element {
				background-color: $red; } } }
	&__container {
		display: flex;
		flex-direction: row;
		justify-content: center;
		padding-top: 60px;
		padding-bottom: 55px;
		@include open-extrabold;
		font-size: 24px;
		color: #000000;
		line-height: 30px;
		text-transform: uppercase;
		@include r(1279) {
			padding-top: 45px;
			padding-bottom: 50px;
			flex-wrap: wrap; }
		@include r(767) {
			padding-top: 40px;
			padding-bottom: 45px;
			font-size: 12px;
			line-height: 18px; } }
	&__col {
		display: flex;
		flex-direction: row;
		flex-basis: 50%;
		@include r(1279) {
			flex-basis: 100%; }
		&:nth-child(1) {
			justify-content: flex-end;
			@include r(1279) {
				justify-content: center; }
			@include r(767) {
				justify-content: flex-start; } }
		&:nth-child(2) {
			padding-left: 90px;
			justify-content: flex-start;
			@include r(1279) {
				justify-content: center;
				margin-top: 20px;
				padding-left: 0; }
			@include r(767) {
				margin-top: 25px; } } }
	&__form {
		display: flex;
		flex-direction: row;
		justify-content: center;
		flex-wrap: wrap;
		@include r(767) {
			display: block; } }
	&__input {
		display: none;
		@include r(1279) {
			display: block; }
		@include r(767) {
			display: table; }
		input {
			display: block;
			width: 235px;
			height: 60px;
			padding-left: 10px;
			padding-right: 10px;
			background-color: #ffffff;
			-webkit-appearance: none;
			@include pt;
			font-size: 16px;
			color: $gray-light;
			text-align: center;
			@include r(767) {
				width: 290px; } } }
	&__btn {
		margin-top: 5px;
		cursor: pointer;
		@include r(1279) {
			margin-top: 0;
			margin-left: 25px;
			pointer-events: none; }
		@include r(767) {
			margin-left: 0;
			margin-top: 20px;
			display: table; }
		input {
			position: absolute;
			z-index: 99;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			background-color: transparent;
			color: transparent;
			-webkit-appearance: none;
			pointer-events: none;
			@include r(1279) {
				pointer-events: auto; } } }
	&__text {
		max-width: 400px;
		@include r(1279) {
			max-width: none; }
		.motion {
			&__element {
				background-color: $red; } } } }


/* ===================================
	 Doctors Page
	 @mixin ================================== */
.doctors {
	&__container {
		padding-top: 95px;
		padding-bottom: 60px; } }


