/* nav-sub ----------------------------------- */
.nav-sub { font-family: "OpenSans-Extrabold", Arial, sans-serif; font-size: 12px; color: #000000; line-height: 18px; text-transform: uppercase; transition: all 0.5s ease-in-out; }

