body { display: flex; flex-direction: column; min-height: 100vh; position: relative; min-width: 320px; font-family: "PTSans-Regular", Arial, sans-serif; font-size: 14px; color: #000; line-height: 1.2; -webkit-font-smoothing: antialiased; background-color: #ffffff; }

body.is-overflow { position: fixed; overflow: hidden; top: 0; left: 0; right: 0; }

.wrapper { position: relative; overflow: hidden; flex: 1 0 auto; }

.main { position: relative; }

