.preloader { position: fixed; top: 0; left: 0; right: 0; bottom: 0; z-index: 1000; background-color: #ffffff; transition: all 1s ease-in-out; }

.preloader__container { position: absolute; z-index: 1; width: 240px; top: 50%; left: 50%; transform: translate(-50%, -50%) scale(1); transition: transform 1s ease-in-out, opacity 0.25s linear; }

.preloader__rect { position: absolute; z-index: 2; top: 0; left: 0; right: 0; background-color: #ffffff; height: 61px; overflow: hidden; }

.preloader__rect:before { content: ''; position: absolute; z-index: 3; top: 5px; left: 0; height: 50px; width: 100%; background-color: #ffffff; animation: preloader-text 0.75s forwards linear; }

.preloader__rect-top { position: absolute; z-index: 4; top: 0; height: 6px; left: 0; right: 0; background-color: #ffffff; animation: preloader-top 0.25s forwards ease-in-out; animation-delay: 0.75s; }

.preloader__rect-bottom { position: absolute; z-index: 4; bottom: 0; height: 6px; left: 0; right: 0; background-color: #ffffff; animation: preloader-bottom 0.25s forwards ease-in-out; animation-delay: 1.24s; }

.preloader__rect-left { position: absolute; z-index: 4; top: 0; width: 6px; left: 0; bottom: 0; background-color: #ffffff; animation: preloader-left 0.25s forwards ease-in-out; animation-delay: 0.99s; }

.preloader__rect-right { position: absolute; z-index: 4; top: 0; width: 6px; right: 0; bottom: 0; background-color: #ffffff; animation: preloader-right 0.25s forwards ease-in-out; animation-delay: 1.49s; }

