@keyframes preloader-top { 0% { transform: translateX(0); }
  100% { transform: translateX(-100%); } }

@keyframes preloader-left { 0% { transform: translateY(0); }
  100% { transform: translateY(100%); } }

@keyframes preloader-bottom { 0% { transform: translateX(0); }
  100% { transform: translateX(100%); } }

@keyframes preloader-right { 0% { transform: translateY(0); }
  100% { transform: translateY(-100%); } }

@keyframes preloader-text { 0% { transform: translateX(0); }
  10% { transform: translateX(0); }
  75% { transform: translateX(100%); }
  100% { transform: translateX(100%); } }

