.subscribe__input { display: none; }

@media only screen and (max-width: 1279px) { .subscribe__input { display: block; } }

@media only screen and (max-width: 767px) { .subscribe__input { display: table; } }

.subscribe__input input { display: block; width: 235px; height: 60px; padding-left: 10px; padding-right: 10px; background-color: #ffffff; -webkit-appearance: none; font-family: "PTSans-Regular", Arial, sans-serif; font-size: 16px; color: #a2a4a8; text-align: center; }

@media only screen and (max-width: 767px) { .subscribe__input input { width: 290px; } }

.subscribe__btn { margin-top: 5px; cursor: pointer; }

@media only screen and (max-width: 1279px) { .subscribe__btn { margin-top: 0; margin-left: 25px; pointer-events: none; } }

@media only screen and (max-width: 767px) { .subscribe__btn { margin-left: 0; margin-top: 20px; display: table; } }

.subscribe__btn input { position: absolute; z-index: 99; top: 0; left: 0; right: 0; bottom: 0; width: 100%; height: 100%; background-color: transparent; color: transparent; -webkit-appearance: none; pointer-events: none; }

@media only screen and (max-width: 1279px) { .subscribe__btn input { pointer-events: auto; } }

.subscribe__text { max-width: 400px; }

@media only screen and (max-width: 1279px) { .subscribe__text { max-width: none; } }

.subscribe__text .motion__element { background-color: #a70a3f; }

/* =================================== Doctors Page @mixin ================================== */
.doctors__container { padding-top: 95px; padding-bottom: 60px; }

/* =================================== Other Styles @mixin ================================== */
.popup { position: fixed; top: 0; right: 0; bottom: 0; left: 0; opacity: 0; visibility: hidden; transition: opacity .3s ease, transform .5s ease; will-change: opacity, transform; z-index: 1050; background-color: rgba(0, 0, 0, 0.5); overflow-x: hidden; overflow-y: auto; -webkit-overflow-scrolling: touch; outline: 0; }

.popup__container { position: relative; background-color: white; box-shadow: 0 3px 24px rgba(0, 0, 0, 0.12); padding: 60px 60px 55px 60px; margin: 30px auto; max-width: 420px; transition: opacity .2s ease, transform .3s ease, visibility 0s .1s; transform: translate3d(0, -50px, 0); will-change: transform, opacity; }

@media (max-width: 767px) { .popup__container { margin: 10px auto; } }

.popup__close-btn { position: absolute; top: 30px; right: 30px; width: 18px; height: 18px; cursor: pointer; padding-top: 8px; }

.popup__close-btn span { display: block; width: 100%; height: 1px; background-color: #d0d0d0; }

.popup__close-btn span:first-of-type { transform: rotate(45deg); }

.popup__close-btn span:last-of-type { transform: rotate(-45deg) translateY(-1px); }

.popup__title { font-family: 'OpenSans-Extrabold', sans-serif; font-size: 18px; font-weight: bold; color: black; }

.popup__content { width: 100%; max-width: 300px; margin: 0 auto; }

.popup__content-padding { padding-top: 20px; }

.popup__content-wide { max-width: 100%; }

.popup__btn { width: 100%; margin-top: 30px; transition: all 0.5s ease-out; }

@media only screen and (max-width: 1279px) { .popup__btn { width: 100%; } }

.popup__form { margin-top: 30px; margin-bottom: 30px; }

.popup__form input, .popup__form textarea { width: 100%; height: 60px; border: 1px solid #e3e3e4; padding-left: 20px; font-family: 'PTSans-Regular', sans-serif; font-size: 16px; }

.popup__form textarea { resize: vertical; height: 100px; padding-top: 20px; box-sizing: border-box; }

.popup__form .form-group { display: inline-block; width: 100%; }

.popup__form .form-group:not(:first-child) { margin-top: 10px; }

.popup__footer { font-family: 'OpenSans-Extrabold', sans-serif; }

.popup__footer span { display: inline-block; width: 100%; color: black; font-size: 12px; font-weight: 700; line-height: 18px; text-transform: uppercase; margin-bottom: 10px; }

.popup__footer a { color: #a2a4a8; font-size: 35px; font-weight: 700; text-transform: uppercase; }

.popup-subscribe .popup__title { font-size: 12px; }

.popup-thankyou img { float: right; }

.popup-to_get .popup__container { max-width: 840px; }

@media (max-width: 767px) { .popup__container { width: 100%; padding: 40px 15px 35px; }
  .popup__close-btn { top: 15px; right: 15px; } }

body.popup-opened { overflow: hidden; }

.popup.opened { opacity: 1; visibility: visible; }

.popup.opened .popup__container { transform: translate3d(0, 0, 0); opacity: 1; visibility: visible; }

textarea.textarea-small { height: 80px; }

.text-block:not(:first-child) { margin-top: 35px; }

.text-block__title { display: flex; align-items: center; justify-content: flex-start; }

.text-block__title-icon { margin-right: 15px; }

.text-block__title span { color: #a70a3f; font-family: 'OpenSans-Extrabold', sans-serif; font-size: 12px; font-weight: 700; line-height: 18px; text-transform: uppercase; }

.text-block__list { margin-top: 20px; color: #808080; font-family: 'PTSans-Regular', sans-serif; font-size: 16px; font-weight: 400; }

.text-block__list_item { position: relative; margin-top: 20px; }

.text-block__list_item:before { content: ''; position: absolute; left: -15px; top: 6px; width: 3px; height: 3px; background-color: #a7a7a7; }

@media (max-width: 767px) { .text-block__list_item { padding-left: 15px; }
  .text-block__list_item:before { left: 0; } }
